export default function (path) {
  if (!path) {
    return {
      images: true,
      video: false
    }
  }
  let pathName = path.toLowerCase() // 转化为小写
  const images = ['jpg', 'jpeg', 'png', 'bmp', 'gif']
  const video = ['mp4', 'mpeg4', 'mpg', 'avi', 'rmvb', 'flv', 'rm', 'mov', 'mtv', 'dat', 'wmv', '3gp']
  return {
    images: images.some(v => v === pathName.substring(pathName.lastIndexOf('.') + 1)),
    video: video.some(v => v === pathName.substring(pathName.lastIndexOf('.') + 1))
  }
}



