<template>
  <div class="detail">
    <div class="img-content" id="imgContent" @click="hideImg">
      <img :src="imgContent" /><span></span>
    </div>
    <div id="contentMiddle">
      <div @click="hideChoice">
        <!-- <div class="head">
          <img id="back" @click="back" src="../assets/back_black.png" />
          <span class="font">私人订制套餐</span>
        </div> -->
        <div class="swiper-container gallery-top">
          <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="item in swiperTop" :key="item.scenePackageId" @click="openImg(item)">
              <swiper-video :coverUrl="item.sceneCardImage" :uid="item.scenePackageId" :url="item.video" v-if="fileMP4(item.video).video"></swiper-video>
              <img :src="item.video" v-if="fileMP4(item.video).images" />
            </div>
          </div>
        </div>
        <div class="swiper-container gallery-thumbs">
          <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="item in galleryThumbs" :key="item.scenePackageId" :style='{background:"url("+item.coverImage+") center top/100% 100% no-repeat"}'>
              <!-- <b>{{item.name}}</b> -->
            </div>
          </div>
        </div>
        <div class="low-color"></div>
        <div class="content detail">
          <h3>
            ——
            <span class="detail-t">套餐详情</span> ——
          </h3>
          <div class="item" v-for="item in swiperTop" :key="item.scenePackageId">
            <img :src="item.sceneCardImage" />
          </div>
        </div>
      </div>
      <div class="content bottom">
        <div class="package">
          <div>{{salePackageName}}套餐</div>
          <span class="money">
            <span class="normal">¥{{priceRange[0]}}</span>
            <span class="decoration">¥{{priceRange[1]}}</span>
          </span>
        </div>
        <div class="btn" @click="openChoice" v-if="hasHouse">开启我的{{salePackageName}}</div>
        <div class="btn gray" v-else>暂不支持当前房型</div>
      </div>
      <div class="choice-content" v-if="hideOrShow">
        <div class="pannel">
          <h3>套餐定制</h3>
          <div class="btn-group">
            <div ref="setCustom" :id="item.scenePkgName" v-for="(item,index) in setCustom" :key="index" class="btn black" @click="btnBlack(item, $event)">
              {{item.scenePkgName}}
              <div class="required" v-if="item.must==1">必选</div>
            </div>
          </div>
        </div>
        <hr />
        <div class="pannel detail-i">
          <h3>面板品牌</h3>
          <div class="btn-group">
            <div v-for="(item,index) in panelBrand" :key="index" :class="item.name == currentPanelB ? 'border btn grey' : 'btn grey' " @click="btnChoice(item,'currentPanelB')">{{item.name}}</div>
          </div>
        </div>
        <div class="pannel detail-i">
          <h3>面板颜色</h3>
          <div class="btn-group">
            <div v-for="(item,index) in panelColor" :key="index" :class="item == currentPanelC ? 'border btn grey' : 'btn grey' " @click="btnChoice(item,'currentPanelC')">
              <!-- <img src="https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/dianyajin%402x.png" /> -->
              {{item}}
            </div>
          </div>
        </div>
        <div class="pannel">
          <h3>门锁颜色</h3>
          <div class="btn-group">
            <div v-for="(item,index) in doorLockColor" :key="index" :class="item.name == current ? 'border btn grey' : 'btn grey' " @click="btnChoice(item.name,'current')">
              <!-- <img src="https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/dianyajin%402x.png" /> -->
              {{item.name}}
            </div>
          </div>
        </div>
        <div class="btn-parent">
          <div class="btn" @click="confirm" v-if="ifConfirm">确定</div>
          <div class="btn gray" @click="confirm" v-else>确定</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import swiperVideo from './component/video'
import Swiper from 'swiper'
import { Message } from 'iview'
import 'swiper/dist/css/swiper.css'
import appConfig from '../libs/app.config'
export default {
  name: 'detail',
  components: { swiperVideo, Message },
  provide: function () {
    return {
      vidioParam: this
    }
  },
  created: function () {
    this.$on('vidioplay', id => {
      this.videos.filter(item => item.name !== id).forEach(video => {
        video.player.pause()
      })
    })
  },
  data () {
    return {
      ifConfirm: false, // 是否可以提交
      salePackageId: null,
      imgContent: '', // 弹出层图片
      hideOrShow: false,
      hasHouse: true, // 是否支持当前房型
      current: '',
      currentPanelC: '',
      currentPanelB: '',
      greyAble: false,
      salePackageName: '', // 套餐名字
      priceRange: [], // 套餐价格
      scenePkgList: [], // 场景总包
      setCustom: [], // 套餐定制
      panelBrand: [], // 面板品牌
      panelColor: [], // 面板颜色
      doorLockColor: [], // 门锁颜色
      swiperTop: [],
      galleryThumbs: []
    }
  },
  mounted () {
    document.body.scrollTop = 0
    document.documentElement.scrollTop = 0
    document.getElementById('imgContent').style.display = 'none'
    this.salePackageId = this.$route.params.salePackageId ? this.$route.params.salePackageId : ''
    this.initData(this.init)
  },
  methods: {
    init () {
      // eslint-disable-next-line no-new
      let galleryThumbs = new Swiper('.gallery-thumbs', {
        spaceBetween: 20,
        slidesPerView: 3,
        loop: false,
        freeMode: true,
        loopedSlides: 5, // looped slides should be the same
        watchSlidesVisibility: true,
        watchSlidesProgress: true,

        observer: true, // 修改swiper自己或子元素时，自动初始化swiper
        observeParents: true // 修改swiper的父元素时，自动初始化swiper
      })
      // eslint-disable-next-line no-new
      new Swiper('.gallery-top', {
        spaceBetween: 10,
        loop: true,
        observer: true, // 修改swiper自己或子元素时，自动初始化swiper
        observeParents: true, // 修改swiper的父元素时，自动初始化swiper
        loopedSlides: 5, // looped slides should be the same
        thumbs: {
          swiper: galleryThumbs
        }
      })
    },
    // 初始化页面数据
    initData (callback) {
      let _self = this
      let params = {
        salePackageId: this.salePackageId
      }
      _self.$http.get('/optional/h5/scene_package/list', params).then(res => {
        if (res && res.data.code === 200) {
          this.swiperTop = res.data.data.list
          this.galleryThumbs = res.data.data.list
          this.salePackageName = res.data.data.salePackageName
          this.priceRange = res.data.data.priceRange ? res.data.data.priceRange.split('-') : []
          this.$nextTick(callback)
        } else {
          Message.error(res.data.msg)
        }
      })
    },
    hideChoice () {
      this.hideOrShow = false
      document.getElementById('app').style.overflowY = 'auto'
    },
    btnChoice (item, current) {
      if (current === 'current') {
        this.current = item
      } else if (current === 'currentPanelC') {
        this.currentPanelC = item
      } else {
        this.currentPanelB = item.name
        this.panelColor = item.color.split(',')
      }
      if (this.current === '' || this.currentPanelC === '' || this.currentPanelB === '') {
        this.ifConfirm = false
      } else {
        this.ifConfirm = true
      }
    },
    btnBlack (item, e) {
      if (item.must === 1) return
      if (e.srcElement.classList.length === 2) {
        e.srcElement.className = 'btn black grey'
      } else {
        e.srcElement.className = 'btn black'
      }
    },
    openChoice () {
      let params = {
        // salePkgId: 14,
        salePkgId: this.salePackageId,
        projectId: appConfig['METHODS'].getCookie('pid'),
        orderType: 2
      }
      let _self = this
      _self.$http.get('/optional/h5/order/getProductSalePkg', params).then(res => {
        if (res && res.data.code === 200) {
          _self.scenePkgList = res.data.data.scenePkgList
          _self.setCustom = []
          let obj = {}
          for (var i = 0; i < _self.scenePkgList.length; i++) {
            if (!obj[_self.scenePkgList[i].scenePkgId]) {
              _self.setCustom.push(_self.scenePkgList[i])
              obj[_self.scenePkgList[i].scenePkgId] = true
            }
          }
          _self.doorLockColor = JSON.parse(res.data.data.skuConfig).lock
          _self.panelBrand = JSON.parse(res.data.data.skuConfig).panel
          _self.panelColor = JSON.parse(res.data.data.skuConfig).panel[0].color.split(',')

          document.getElementById('app').style.overflowY = 'hidden'
          _self.hideOrShow = true
        } else {
          Message.error(res.data.msg)
        }
      })
    },
    confirm () {
      let _self = this
      if (_self.ifConfirm === false) return
      let scenePkgNameParam = ''
      let scenePkgIdList = []
      let amount = 0
      _self.$refs.setCustom.forEach(function (item) {
        if (item.className === 'btn black') {
          scenePkgNameParam += item.id + '、'
          _self.scenePkgList.forEach(function (itemS) {
            if (item.id === itemS.scenePkgName && itemS.brand === _self.currentPanelB) {
              amount += itemS.amount
              scenePkgIdList.push(itemS.scenePkgId)
            }
          })
        }
      })
      let params = {
        salePkgId: this.salePackageId, // 销售包id
        planColor: _self.currentPanelC,
        planBrand: _self.currentPanelB,
        lockColor: _self.current,
        salePackageName: _self.salePackageName,
        scenePkgName: scenePkgNameParam !== '' ? scenePkgNameParam.slice(0, scenePkgNameParam.length - 1) : '',
        orderMoney: amount,
        scenePkgIdList: scenePkgIdList
      }
      document.getElementById('app').style.overflowY = 'auto'
      this.$store.dispatch('changeParam', params)
      _self.$router.push({ name: 'order', params: {} })
    },
    back () {
      history.go(-1)
    },
    openImg (item) {
      if (item.video.slice(item.video.length - 3, item.video.length) === 'mp4') return
      this.imgContent = item.video
      document.getElementById('imgContent').style.display = 'block'
      document.getElementById('contentMiddle').style.display = 'none'
    },
    hideImg () {
      this.imgContent = ''
      document.getElementById('imgContent').style.display = 'none'
      document.getElementById('contentMiddle').style.display = 'block'
    }
  }
}
</script>

<style lang="less">
.detail {
  width: 100%;
  height: 100%;
  background-color: #fff;
  position: relative;
  text-align: center;
  .img-content {
    width: 100%;
    height: 100%;
    background: #000;
    position: relative;
    z-index: 9999;
    overflow: auto;
    img {
      width: 100%;
      vertical-align: middle;
    }
    span {
      display: inline-block;
      height: 100%;
      vertical-align: middle;
    }
  }
  .head {
    text-align: center;
    height: 44px;
    font-size: 16px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: #333;
    line-height: 44px;
    position: fixed;
    width: 100%;
    top: 0;
    background: #fff;
    z-index: 999;
    img {
      width: 22px;
      float: left;
      margin-top: 12px;
      margin-left: 10px;
    }
    .font {
      display: inline-block;
      margin-left: -42px;
    }
  }
  .low-color {
    height: 10px;
    background: #f2f2f2;
  }
  .bottom {
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: center;
    background-color: #fff;
    padding-top: 10px;
    padding-bottom: 30px;
    width: 100%;
    box-shadow: 0px -2px 18px 0px rgba(0, 0, 0, 0.14);
    z-index: 999;
    .package {
      // width: 119px;
      font-size: 14px;
      font-family: PingFangSC-Medium;
      font-weight: 500;
      color: #444444;
      line-height: 20px;
      text-align: left;
    }
    .money {
      display: inline-block;
      font-size: 12px;
      font-family: PingFangSC-Regular;
      font-weight: 400;
      color: #333;
    }
    .decoration {
      text-decoration: line-through;
      font-size: 10px;
      color: #acacac;
    }
    // .normal {
    // }
    .btn {
      width: 140px;
      height: 44px;
      background: #df3427;
      box-shadow: 0px 2px 4px 0px rgba(223, 52, 39, 0.2);
      font-size: 12px;
      font-family: PingFangSC-Medium;
      font-weight: 500;
      color: #fff;
      line-height: 44px;
      text-align: center;
      margin-left: 70px;
    }
    .gray {
      background: #a7a7a7;
      box-shadow: 0px -2px 18px 0px rgba(0, 0, 0, 0.14);
    }
  }
  .detail {
    background: #fff;
    h3 {
      color: #9b9b9b;
      font-size: 12px;
      font-family: PingFangSC-Regular;
      font-weight: 400;
      text-align: center;
      line-height: 30px;
    }
    .item {
      img {
        width: 100%;
      }
    }
  }
  .choice-content {
    width: 100%;
    position: fixed;
    background: #fff;
    bottom: 0;
    padding-top: 9px;
    z-index: 999;
    height: 73%;
    overflow: auto;
    hr {
      background: #dedede;
      height: 1px;
      border: 1px;
    }
    .pannel {
      padding: 0 19px;
      h3 {
        font-size: 14px;
        font-family: PingFangSC-Regular;
        font-weight: 400;
        color: #8c8c8c;
        line-height: 20px;
        text-align: left;
        margin-top: 10px;
      }
      .btn-group {
        display: flex;
        flex-wrap: wrap;
        .btn {
          width: 85px;
          height: 31px;
          margin: 10px;
          font-size: 12px;
          font-family: PingFangSC-Regular;
          font-weight: 400;
          line-height: 31px;
          border: 1px solid #f8f8f8;
        }
        .black {
          color: #fff;
          background: #444444;
          position: relative;
          .required {
            position: absolute;
            width: 28px;
            padding: 1px;
            vertical-align: middle;
            height: 16px;
            background: #ff5959;
            border-radius: 8px;
            font-size: 10px;
            font-family: PingFangSC-Medium;
            font-weight: 500;
            color: #fff;
            line-height: 16px;
            top: -8px;
            right: -14px;
            // padding: 2px 3px;
          }
        }
        .grey {
          color: #666666;
          background: #f8f8f8;
          img {
            width: 19px;
            height: 19px;
            vertical-align: middle;
            padding-right: 9px;
          }
        }
      }
    }
    .detail-i {
      .btn-group {
        border-bottom: #dedede 1px solid;
        padding-bottom: 10px;
      }
    }
    .btn-parent {
      display: flex;
      justify-content: center;
      margin: 23px 0;
      .gray {
        // background: #666666;
        background: #d8d8d8;
        color: #666666;
      }
    }
    .btn {
      width: 345px;
      background: #df3427;
      font-size: 15px;
      font-family: PingFangSC-Medium;
      font-weight: 500;
      color: #fff;
      line-height: 44px;
    }
    .border {
      border: 1px solid #333333 !important;
    }
  }
  .gallery-top {
    .swiper-slide {
      width: 375px;
      height: 483px;
      font-size: 14px;
      font-family: PingFangSC-Medium;
      font-weight: 500;
      color: #fff;
      line-height: 20px;
      position: relative;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .swiper-container {
      width: 100% !important;
      height: 483px !important;
    }
  }
  // .gallery-thumbs {
  //   // padding: 10px 0 10px 20px;
  //   padding: 10px 0;
  //   margin-top: -54px;
  //   .swiper-slide {
  //     width: 140px !important;
  //     height: 100px;
  //     position: relative;
  //     font-size: 14px;
  //     font-family: PingFangSC-Medium;
  //     font-weight: 500;
  //     color: #fff;
  //     line-height: 20px;
  //     b {
  //       position: absolute;
  //       left: 7px;
  //       top: 65%;
  //       width: 60px;
  //     }
  .gallery-thumbs {
    padding: 10px 0;
    margin-top: -54px;
    .swiper-slide {
      width: 140px !important;
      height: 100px;
      position: relative;
      font-size: 14px;
      font-family: PingFangSC-Medium;
      font-weight: 500;
      color: #fff;
      line-height: 20px;
      b {
        position: absolute;
        left: 7px;
        top: 65%;
        width: 60px;
      }
    }
    .swiper-slide-thumb-active {
      border: 3px solid #f46360;
    }
    .swiper-container {
      width: 100% !important;
      height: 100px !important;
    }
  }
}
</style>



