<template>
  <div class="contents">
    <div class="text-none" v-if="order.length<=0">暂无订单</div>
    <div v-for="item in order" :key="item.id" class="item" v-else>
      <div class="title">
        <span>{{controlDate(item.updateTime)}}</span>
        <span class="font-h">{{item.orderStatusLabel}}</span>
      </div>
      <div class="middle">
        <div class="middle-left">
          <img :src="item.coverImage" v-if="item.coverImage !==null" />
          <div class="img-null" v-else>暂无图片</div>
          <div class="middle-center">
            <div class="font-h">套餐名:{{item.salePackageName}}</div>
            <div class="space-top">品牌：{{item.masterBrand}}</div>
          </div>
        </div>
        <div class="middle-right">
          <div>{{item.orderMoney}}</div>
          <div class="space-top">x1</div>
        </div>
      </div>
      <div class="foot">
        <span>
          <span>共1件</span>
          <span>应付总额：{{item.realMoney}}</span>
        </span>
        <span class="btn" @click="toResult(item.id)">查看进展</span>
      </div>
      <div class="grey-bg"></div>
    </div>
  </div>
</template>

<script>
import appConfig from './../../libs/app.config'
export default {
  props: {
    order: {
      type: Array,
      required: false,
      // eslint-disable-next-line vue/require-valid-default-prop
      default: []
    }
  },
  methods: {
    controlDate (data) {
      // let moment = require('moment')
      // let item = moment(data).format('L')
      let item = data !== '' ? appConfig['METHODS'].formatDateTime(data, 'y-m-d') : ''
      return item
    },
    toResult (id) {
      this.$router.push({ name: 'result', params: { id: id } })
    }
  }
}
</script>

<style lang="less">
.contents {
  .text-none {
    text-align: center;
    margin-top: 50px;
  }
  .item {
    margin-bottom: 10px;
  }
  .title {
    font-size: 14px;
    line-height: 40px;
    .font-h {
      color: #666666;
      font-family: PingFangSC;
      font-weight: 500;
    }
  }
  .foot {
    font-size: 14px;
    color: #5b5a5a;
    line-height: 52px;
    height: 52px;
    .btn {
      color: #333;
      border-radius: 17px;
      line-height: 30px;
      display: table;
      padding: 0 19px;
      border: 1px solid #807f7e;
      margin-top: 10px;
    }
  }
  .title,
  .foot {
    margin: 0 14px;
    display: flex;
    justify-content: space-between;
  }
  .middle {
    display: flex;
    justify-content: space-between;
    color: #5b5a5a;
    border-bottom: 1px #e3e3e3 solid;
    border-top: 1px #e3e3e3 solid;
    padding: 14px 0;
    .middle-left {
      display: inline-flex;
      margin-left: 10px;
      img,
      .img-null {
        width: 127px;
        height: 83px;
      }
      .img-null {
        display: inline-block;
        border: 1px solid #ddd;
        line-height: 83px;
        text-align: center;
      }
    }
    .middle-center {
      margin-left: 12px;
      font-weight: 400;
      .font-h {
        font-size: 14px;
        font-family: PingFangSC;
        font-weight: 500;
      }
    }
    .middle-right {
      font-size: 14px;
      margin-right: 10px;
    }
    .space-top {
      display: inline-block;
      margin-top: 6px;
    }
  }
}
</style>



