  <div class="index">
    <div class="index">
      <div class="swiper-container top-swiper">
        <div class="head">
          <!-- <span class="font">理想家</span> -->
          <span id="indent" @click="toIndent"><img src="../assets/icon_dingdan@2x.png" />订单</span>
        </div>
        <div class="swiper-wrapper">
          <div v-for="item in slideTop" :key="item.id" class="swiper-slide">
            <div class="img-pannel">
              <div class="pic" :style="{background: `url(${item.imgUrl}) no-repeat center`}"></div>
              <!-- <img :src="item.imgUrl" /> -->
            </div>
            <!-- <div class="detail-pannel">
              <span class="title-s">{{item.titleS}}</span>
              <span class="introduction-s">{{item.introductionS}}</span>
            </div> -->
          </div>

        </div>
        <div class="swiper-pagination"></div>
      </div>

      <div class="content recommended">
        <div class="swiper-container recommended-container">
          <div class="swiper-wrapper">
            <div class="swiper-slide item">
              <img src="https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/new_img%402x.png" />
              <div></div>
            </div>
            <div v-for="item in slideMiddle" :key="item.id" class="swiper-slide item">
              <img :src="item.imgUrl" />
              <div class="item-font">{{item.name}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="content plan">
        <h3>
          ——
          <span class="plan-t">推荐方案</span> ——
        </h3>
        <div class="btn-detail" v-for="item in planT" :key="item.salePackageId" @click="toDetail(item.salePackageId)">
          <img :src="item.saleCardImage" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Swiper from 'swiper'
import { Message } from 'iview'
import 'swiper/dist/css/swiper.css'
import appConfig from '../libs/app.config'
export default {
  name: 'index',
  components: { Message },
  data () {
    return {
      planT: [
        // {
        //   salePackageId: '1',
        //   saleCardImage: 'https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/img_1%402x.png',
        //   coverImage: ''
        // },
        // {
        //   salePackageId: '2',
        //   saleCardImage: 'https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/img_1%402x.png',
        //   coverImage: ''
        // },
        // {
        //   salePackageId: '3',
        //   saleCardImage: 'https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/img_1%402x.png',
        //   coverImage: ''
        // }

      ],
      slideTop: [
        {
          id: '1',
          // imgUrl: 'https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/baike1_img%403x.png',

          imgUrl: 'https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/banner%403x.png',
          titleS: '一分钟智能家居小百科3',
          introductionS: '方便·节能·安全,智慧改变即刻开始'
        }
        // {
        //   id: '2',
        //   imgUrl: 'https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/img_2%402x.png',
        //   titleS: '一分钟智能家居小百科3',
        //   introductionS: '方便·节能·安全,智慧改变即刻开始'
        // },
        // {
        //   id: '3',
        //   imgUrl: 'https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/img_3%402x.png',
        //   titleS: '一分钟智能家居小百科3',
        //   introductionS: '方便·节能·安全,智慧改变即刻开始'
        // }
      ],
      slideMiddle: [
        {
          id: '1',
          imgUrl: 'https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/%E5%85%A5%E4%BE%B5%E6%8A%A5%E8%AD%A6%403x.png',
          name: '入侵报警'
        },
        {
          id: '2',
          imgUrl: 'https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/%E6%97%A0%E6%84%9F%E7%81%AF%E5%85%89%403x.png',
          name: '无感灯光'
        },
        {
          id: '3',
          imgUrl: 'https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/%E5%AE%89%E9%98%B2%E5%AE%88%E6%8A%A4%403x.png',
          name: '安防守护'
        },
        {
          id: '4',
          imgUrl: 'https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/%E8%88%92%E9%80%82%E7%8E%AF%E5%A2%83%403x.png',
          name: '舒适环境'
        },
        {
          id: '5',
          imgUrl: 'https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/%E6%97%A0%E6%84%9F%E9%80%9A%E8%A1%8C%403x.png',
          name: '无感通行'
        }
      ]
    }
  },
  mounted () {
    let pid = this.$route.query.pid !== undefined ? this.$route.query.pid : 14
    let hid = this.$route.query.hid !== undefined ? this.$route.query.hid : 8
    let username = '王小明'
    let phone = '18777777777'
    let address = '浙江省杭州市拱墅区甲乙路丙丁小区XX栋201室'
    appConfig['METHODS'].setCookie('pid', pid)
    appConfig['METHODS'].setCookie('hid', hid)
    appConfig['METHODS'].setCookie('username', username)
    appConfig['METHODS'].setCookie('phone', phone)
    appConfig['METHODS'].setCookie('address', address)
    this.init()
    this.initData()
    document.body.scrollTop = 0
    document.documentElement.scrollTop = 0
  },
  methods: {
    init () {
      // eslint-disable-next-line no-new
      new Swiper('.top-swiper', {
        speed: 300,
        autoplay: {
          delay: 2000,
          disableOnInteraction: false
        },
        loop: true,
        slidesPerView: 1,
        spaceBetween: 0,
        centeredSlides: true,
        initialSlide: 1,
        grabCursor: true
        // pagination: {
        //   el: '.swiper-pagination',
        //   clickable: true
        // }
      })
      // eslint-disable-next-line no-new
      new Swiper('.recommended-container', {
        slidesPerView: 4,
        spaceBetween: 8
      })
    },
    // 初始化页面数据
    initData () {
      let _self = this
      let projectId = appConfig['METHODS'].getCookie('pid')
      let houseId = appConfig['METHODS'].getCookie('hid')
      let params = {
        projectId: projectId,
        houseId: houseId
      }
      _self.$http.get('/optional/h5/sale_package/list', params).then(res => {
        if (res && res.data.code === 200) {
          _self.planT = res.data.data
        } else {
          Message.error(res.data.msg)
        }
      })
    },
    toDetail (salePackageId) {
      this.$router.push({ name: 'detail', params: { salePackageId: salePackageId } })
    },
    toIndent () {
      this.$router.push({ name: 'indent', params: {} })
    }
  }
}
</script>

<style lang="less">
