      // margin-left: 41px;
    }
  }
  .top-swiper {
    height: 324px;
    background: url(https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/bg_img%402x.png)
      no-repeat center top;
    background-size: 100% 76%;
    .swiper-slide {
      text-align: center;
      font-size: 18px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      -webkit-justify-content: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      -webkit-align-items: center;
      align-items: center;
      transition: 300ms;
      position: relative;
      height: 286px;
      .detail-pannel {
        position: absolute;
        bottom: 0px;
        width: 304px;
        height: 55px;
        text-align: left;
        padding-top: 7px;
        padding-left: 10px;
        background: #fff;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
        box-shadow: 0px 1px 6px 0px #ccc;
      }
      .title-s {
        display: block;
        font-size: 18px;
        font-family: PingFangSC-Semibold;
        font-weight: 600;
        color: rgba(51, 51, 51, 1);
        line-height: 17px;
      }
      .introduction-s {
        width: 192px;
        height: 17px;
        font-size: 12px;
        font-family: PingFangSC-Light;
        font-weight: 300;
        color: rgba(102, 102, 102, 1);
        line-height: 17px;
      }
    }
    img {
      width: 302px;
      height: 180px;
      border-top-left-radius: 8px;
      border-top-right-radius: 8px;
    }
    // .swiper-slide:not(.swiper-slide-active) {
    //   transform: scale(0.8);
    //   z-index: -999;
    // }
    // .swiper-slide-prev {
    //   margin-left: -155px;
    // }
    // .swiper-slide-next {
    //   margin-left: 162px;
    // }
    // .swiper-slide-active {
    //   // margin-left: 163px;
    //   z-index: 999;
    // }
  }
  .recommended {
    display: flex;
    justify-content: center;
    margin: 0 8px;
    .item {
      flex-shrink: 1;
      img {
        width: 78px;
        margin: 4px;
      }
      .item-font {
        font-size: 12px;
        font-family: PingFangSC-Semibold;
        font-weight: 300;
        color: rgba(102, 102, 102, 1);
        line-height: 17px;
        text-align: center;
      }
    }
  }
  .plan {
    background-color: #f8f8fa;
    margin-bottom: 30px;
    text-align: center;
    h3 {
      font-size: 17px;
