<template>
  <div class="order">
    <!-- <div class="head">
      <img id="back" @click="back" src="../assets/back_black.png" />
      <span class="font">订单确认</span>
      <div class="grey-bg"></div>
    </div> -->
    <div class="content address">
      <div class="items">
        <span class="name">业主：{{username}}</span>
        <span class="phone">{{phone.slice(0, 3)}}****{{phone.slice(phone.length-4, phone.length)}}</span>
      </div>
      <p>安装地址：{{address}}</p>
    </div>
    <div class="grey-bg"></div>
    <div class="content detail">
      <div class="type">
        <div class="type-name">套餐定制：</div>
        <div class="type-intro">{{params.scenePkgName}}</div>
      </div>
      <div class="type">
        <div class="type-name">面板品牌：</div>
        <div class="type-intro">{{params.planBrand}}</div>
      </div>
      <div class="type">
        <div class="type-name">面板颜色：</div>
        <div class="type-intro">{{params.planColor}}</div>
      </div>
      <div class="type">
        <div class="type-name">门锁颜色：</div>
        <div class="type-intro">{{params.lockColor}}</div>
      </div>
    </div>
    <div class="grey-bg"></div>
    <div class="content listing">
      <span class="listing-name">支付方式：</span>
      <span class="btn">
        <span class="online-pay">
          <img src="https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/Combined%20Shape%402x.png" />
          线下支付
        </span>
        <span class="outline-pay">
          <span class="circle"></span>
          在线支付
        </span>
      </span>
    </div>
    <div class="grey-bg"></div>
    <!-- <div class="content agreement">
      <div class="img check" id="check" @click="check"></div>
      <span>已阅读并确认</span>
      <span class="blue">《私人订智产品协议》</span>
    </div> -->
    <div class="content bottom">
      <span class="money">
        <span>合计：</span>
        <span class="symbol">¥</span>
        <span class="red">{{params.orderMoney}}</span>
      </span>
      <span class="btn" id="submit" @click="submit">立即结算</span>
    </div>
  </div>
</template>

<script>
import { Message } from 'iview'
import appConfig from '../libs/app.config'
export default {
  name: 'order',
  components: { Message },
  data () {
    return {
      params: {},
      username: '',
      phone: '',
      address: '',
      houseId: null,
      projectId: null
    }
  },
  mounted () {
    this.init()
    if (this.$store.state.params.salePkgId !== undefined) {
      this.params = this.$store.state.params
      localStorage['params'] = JSON.stringify(this.$store.state.params)
    } else {
      this.params = JSON.parse(localStorage['params'])
    }
    this.username = appConfig['METHODS'].getCookie('username')
    this.phone = appConfig['METHODS'].getCookie('phone')
    // this.address = appConfig['METHODS'].getCookie('address')
    this.getAddress()
    this.houseId = appConfig['METHODS'].getCookie('hid')
    this.projectId = appConfig['METHODS'].getCookie('pid')
  },
  methods: {
    init () { },
    check (e) {
      if (e.srcElement.className === 'img check') {
        e.srcElement.className = 'img nocheck'
      } else {
        e.srcElement.className = 'img check'
      }
    },
    submit () {
      let _self = this
      let params = {
        orderMoney: _self.params.orderMoney,
        houseId: parseInt(_self.houseId),
        projectId: parseInt(_self.projectId),
        phone: _self.phone,
        username: _self.username,
        orderType: 2,
        lockColor: _self.params.lockColor,
        planColor: _self.params.planColor,
        planBrand: _self.params.planBrand,
        salePkgId: _self.params.salePkgId,
        salePackageName: _self.params.salePackageName,
        scenePkgIdList: _self.params.scenePkgIdList,
        address: _self.address

      }
      _self.$http.post('/optional/h5/order/add', params).then(res => {
        if (res && res.data.code === 200) {
          this.$router.push({ name: 'pay', params: params })
        } else {
          Message.error(res.data.msg)
        }
      })
    },
    back () {
      history.go(-1)
    },
    getAddress () {
      let params = {
        houseId: appConfig['METHODS'].getCookie('hid')
      }
      this.$http.get('/optional/h5/order/getAddress', params).then(res => {
        if (res && res.data.code === 200) {
          this.address = res.data.data
        } else {
          Message.error(res.data.msg)
        }
      })
    }
  }
}
</script>

<style lang="less">
.order {
  background: #f2f2f2;
  .head {
    text-align: center;
    height: 44px;
    font-size: 16px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: #333;
    line-height: 44px;
    background: #fff;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 999;
    img {
      width: 22px;
      float: left;
      margin-top: 12px;
      margin-left: 10px;
    }
    .font {
      display: inline-block;
      margin-left: -42px;
    }
  }
  .address {
    // border-top:1px solid #DDDDDD;
    background: #fff;
    height: 101px;
    box-shadow: 0px -1px 0px 0px rgba(241, 241, 241, 1), 0px 1px 0px 0px #f1f1f1;
    padding: 15px 15px 0 15px;
    position: relative;
    // margin-top: 54px;
    .items {
      font-size: 14px;
      font-family: PingFangSC-Medium;
      font-weight: 500;
      color: #333333;
      line-height: 20px;
      .phone {
        float: right;
        margin-right: 10px;
      }
    }
    p {
      font-size: 14px;
      font-family: PingFangSC-Regular;
      font-weight: 400;
      color: #999999;
      line-height: 20px;
      margin: 12px 26px 15px 0;
    }
  }
  .detail {
    padding: 14px;
    // margin: 10px 0;
    background: #fff;
    .type {
      font-size: 14px;
      color: #333;
      display: flex;
      margin: 26px 0;
      justify-content: start;
      position: relative;
      .type-name {
        font-family: PingFangSC-Medium;
        font-weight: 500;
        width: 75px;
        line-height: 20px;
      }
      .type-intro {
        font-family: PingFangSC-Regular;
        font-weight: 400;
        width: 217px;
        line-height: 20px;
        margin-left: 45px;
        margin-right: 8px;
      }
    }
  }
  .listing {
    color: #333333;
    line-height: 60px;
    font-size: 14px;
    display: inline-block;
    width: 100%;
    // margin: 10px -5px;
    background: #fff;
    .listing-name {
      font-family: PingFangSC-Medium;
      font-weight: 500;
      padding-left: 10px;
    }
    .btn {
      color: #333;
      float: right;
      margin-right: 13px;
      .online-pay {
        margin-right: 10px;
        img {
          width: 16px;
          vertical-align: middle;
        }
      }
      .outline-pay {
        opacity: 0.4;
      }
      .circle {
        display: inline-block;
        width: 16px;
        border-radius: 50%;
        height: 16px;
        vertical-align: middle;
        border: 1px #dedede solid;
      }
    }
  }
  .bottom {
    width: 100%;
    position: fixed;
    bottom: 0;
    line-height: 64px;
    background: #fff;
    padding: 10px 0;
    text-align: center;
    box-shadow: 0px -2px 4px 0px #e8e8e8;
    .red {
      font-size: 28px;
      font-family: BebasNeue;
      color: #df3427;
    }
    .symbol {
      font-size: 12px;
      font-family: BebasNeue;
      color: #df3427;
    }
    .money {
      padding-left: 20px;
    }
    .btn {
      width: 172px;
      height: 44px;
      color: #fff;
      text-align: center;
      display: inline-block;
      background: #df3427;
      line-height: 44px;
      margin-left: 35px;
    }
  }
  .agreement {
    line-height: 30px;
    padding: 15px;
    background: #fff;
    font-size: 12px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    .img {
      width: 14px;
      height: 14px;
      vertical-align: sub;
      margin-right: 4px;
      background: url(https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/xuanzhogn_icon%402x.png)
        no-repeat center top;
      background-size: 100% 100%;
      display: inline-block;
    }
    .nocheck {
      background: url(https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/weixuanzhong.png)
        no-repeat center top;
      background-size: 100% 100%;
    }
    .blue {
      color: #4a90e2;
    }
  }
  .grey-bg {
    height: 10px;
    background: #f2f2f2;
  }
}
</style>



