<template>
  <div class="pay">
    <!-- <div class="head">
      <img id="back" @click="back" src="../assets/back_black.png" />
      <span class="font">支付成功</span>
    </div> -->
    <div class="content success">
      <img src="https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/Combined%20Shape%402x.png" />
      <div class="font">您已提交成功，请等待销售人员确认订单，并为您安排施工</div>
      <div class="btn-group">
        <div class="btn grey" @click="toIndex">返回首页</div>
        <div class="btn red" id="result" @click="toResult">预览理想家</div>
      </div>
    </div>
    <div class="content process">
      <div class="title">理想家产品项目流程</div>
      <div class="items-group">
        <div class="item">
          <span class="num">1</span>
          <span class="font">专属下单</span>
        </div>
        <div class="item">
          <span class="num">2</span>
          <span class="font">项目施工</span>
        </div>
        <div class="item">
          <span class="num">3</span>
          <span class="font">随房交付</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'pay',
  data () {
    return {
      params: {}
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
    },
    toResult () {
      this.$router.push({ name: 'result', params: {} })
    },
    toIndex () {
      this.$router.push({ name: 'index', params: {} })
    },
    back () {
      history.go(-1)
    }
  }
}
</script>

<style lang="less">
.pay {
  background: #f2f2f2;
  text-align: center;
  height: 100%;
  .el-scrollbar__thumb {
    display: none;
  }
  .el-scrollbar__wrap {
    overflow-x: hidden;
    overflow-y: auto;
  }
  .head {
    text-align: center;
    height: 44px;
    font-size: 16px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: #333;
    line-height: 44px;
    background: #fff;
    position: fixed;
    width: 100%;
    top: 0;
    border-bottom: 1px solid #dddddd;
    img {
      width: 22px;
      float: left;
      margin-top: 12px;
      margin-left: 10px;
    }
    .font {
      display: inline-block;
      margin-left: -42px;
    }
  }
  .success {
    border-top: 1px solid #dddddd;
    img {
      width: 53px;
      margin-top: 91px;
    }
    .font {
      font-size: 14px;
      font-family: PingFangSC-Regular;
      font-weight: 400;
      line-height: 20px;
      color: #888888;
      margin: 15px 85px 0 85px;
    }
    .btn {
      width: 150px;
      height: 46px;
      line-height: 46px;
      font-size: 14px;
      font-family: PingFangSC-Regular;
      font-weight: 400;
      display: inline-block;
      margin: 30px 8px;
    }
    .grey {
      color: #333333;
      background: #f8f8f8;
      border: 1px solid #ebebeb;
    }
    .red {
      background: #df3427;
      color: #ffffff;
    }
  }
  .process {
    width: 90%;
    height: 100px;
    display: inline-block;
    background: #ffffff;
    box-shadow: 0px 1px 6px 0px rgba(0, 0, 0, 0.05);
    border-radius: 6px;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: #444;
    .title {
      height: 31px;
      line-height: 45px;
      text-align: left;
      margin-left: 20px;
    }
    .items-group {
      display: flex;
      justify-content: space-between;
      margin: 20px;
    }
    .item {
      .num {
        width: 20px;
        height: 20px;
        line-height: 20px;
        border-radius: 50%;
        background: #df3427;
        display: inline-block;
        color: #fff;
      }
    }
  }
}
</style>



