<template>
  <div class="xg-player">
    <div class="mse" :id="uid"></div>
  </div>
</template>

<script>
import Player from 'xgplayer'
export default {
  props: {
    url: {
      type: String,
      required: true
    },
    uid: {
      type: Number,
      required: true
    },
    coverUrl: {
      type: String,
      required: false
    }
  },
  inject: {
    vidioParam: {
      from: 'vidioParam',
      default: undefined
    }
  },
  created: function () {
    if (this.vidioParam && !this.vidioParam.videos) {
      this.vidioParam.videos = []
    }
  },
  mounted () {
    // eslint-disable-next-line no-new
    let p = new Player({
      id: this.uid,
      url: this.url,
      poster: this.coverUrl, // 封面图
      playsinline: true,
      fluid: true
    })
    this.vidioParam.videos.push({ name: this.uid, player: p })
    p.on('play', () => {
      this.vidioParam.$emit('vidioplay', this.uid)
    })
  }
}
</script>

<style lang="less">
.xg-player {
  width: 100%;
  height: 100%;
  background: #000;
  vertical-align: middle;
  .mse {
    height: 85% !important;
    .xgplayer-poster {
      height: 118%;
    }
  }
}
</style>



