<template>
  <div class="result">
    <!-- <div class="head">
      <img id="back" @click="back" src="../assets/back_black.png" />
      <span class="font">我的理想家</span>
    </div> -->
    <div class="content top">
      <span class="call">尊贵的业主：</span>
      <p>我是您的智能管家 ，小智。很高兴与您相见，下面为您汇报理想家进展：</p>
    </div>
    <div class="grey-bg">
    </div>
    <h3>
      <span class="line">————— </span>
      理想家产品项目流程
      <span class="line"> —————</span>
    </h3>
    <div class="content process">
      <div class="step-one">
        <h2 class="one-item">
          <span class="circle red"></span>
          专属理想家定制
        </h2>
        <div class="date-line line-one">
          <img src="https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/Combined%20Shape%402x.png">
          <span class="font">专属理想家定制下单</span>
          <span class="date">2019-06-01</span>
        </div>
        <h2 class="two-item">
          <span class="circle red"></span>
          项目施工
        </h2>
      </div>
      <div class="step-two">
        <div class="date-line line-two">
          <img src="https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/Combined%20Shape%402x.png">
          <span class="font">智能产品安装完毕</span>
          <span class="date">2019-06-05</span>
        </div>
        <div class="date-line line-three">
          <img src="https://greentown-app.oss-cn-shanghai.aliyuncs.com/scene-mall/Combined%20Shape%402x.png">
          <span class="font">智能交互方案调试完成</span>
          <span class="date">2019-06-05</span>
        </div>
        <h2 class="three-item">
          <span class="circle gray"></span>
          随房交付
        </h2>
      </div>

    </div>

  </div>
</template>

<script>
export default {
  name: 'result',
  data () {
    return {
      id: null
    }
  },
  mounted () {
    this.init()
    if (this.$route.params) {
      this.id = this.$route.params.id
    }
  },
  methods: {
    init () {
    },
    back () {
      history.go(-1)
    }
  }
}
</script>

<style lang="less">
.result {
  .head {
    text-align: center;
    height: 44px;
    font-size: 16px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: #333;
    line-height: 44px;
    position: fixed;
    width: 100%;
    top: 0;
    background: #fff;
    z-index: 999;
    border-bottom: 1px solid #dddddd;
    img {
      width: 22px;
      float: left;
      margin-top: 12px;
      margin-left: 10px;
    }
    .font {
      display: inline-block;
      margin-left: -42px;
    }
  }
  .top {
    font-size: 14px;
    // margin-top: 37px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: #444;
    padding: 18px;
    line-height: 20px;
    border-top: 1px solid #dddddd;
    .call {
      line-height: 40px;
    }
  }
  .process {
    margin: 20px 20px 50px 20px;
    height: 273px;
    position: relative;
    .step-one {
      border-left: 1px solid #df3427;
      height: 118px;
    }
    .step-two {
      border-left: 1px solid #df3427;
      height: 152px;
    }
    .gray-border-left {
      border-left-color: #c4c4c4;
    }
    h2 {
      font-size: 16px;
      font-family: PingFangSC-Medium;
      font-weight: 500;
      color: #444444;
      line-height: 22px;
      left: -6px;
      position: absolute;
      .circle {
        display: inline-block;
        width: 12px;
        height: 12px;
        border-radius: 50%;
      }
      .red {
        background: #df3427;
      }
      .gray {
        background: #c4c4c4;
      }
    }
    .one-item {
      top: -5px;
    }
    .two-item {
      top: 105px;
    }
    .three-item {
      top: 263px;
    }
    .date-line {
      color: #777777;
      font-size: 14px;
      font-family: PingFangSC-Regular;
      font-weight: 400;
      img {
        width: 15px;
        height: 15px;
        vertical-align: top;
      }
      .date {
        float: right;
      }
    }
    .line-one {
      padding: 52px 0 0 20px;
    }
    .line-two {
      padding: 45px 0 0 20px;
    }
    .line-three {
      padding: 35px 0 0 20px;
    }
  }
  h3 {
    font-size: 14px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: #444;
    line-height: 65px;
    text-align: center;
  }
  .line {
    color: #e0e0e0;
  }
  .grey-bg {
    height: 10px;
    background: #f2f2f2;
  }
}
</style>



