import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import detail from '@/components/detail'
import order from '@/components/order'
import pay from '@/components/pay'
import result from '@/components/result'
import indent from '@/components/indent'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/detail/:salePackageId',
      name: 'detail',
      component: detail
    },
    {
      path: '/order',
      name: 'order',
      component: order
    },
    {
      path: '/pay',
      name: 'pay',
      component: pay
    },
    {
      path: '/result',
      name: 'result',
      component: result
    },
    {
      path: '/indent',
      name: 'indent',
      component: indent
    }
  ]
})



