<template>
  <div class="indent">
    <!-- <div class="head">
      <img id="back" @click="back" src="../assets/back_black.png" />
      <span class="font">我的订单</span>
    </div> -->
    <div class="grey-bg top-gray"></div>
    <Tabs :value="activeName" @on-click="handleClick">
      <Tab-pane label="全部" name='全部'>
        <my-order :order="allOrder"></my-order>
      </Tab-pane>
      <!-- <Tab-pane label="待支付" name='待支付'>
        <my-order :order="unpaidOrder"></my-order>
      </Tab-pane> -->
      <Tab-pane label="待确认" name='待确认'>
        <my-order :order="unconfirmedOrder"></my-order>
      </Tab-pane>
      <Tab-pane :label="count<=0?'待施工':labelConstruction" name='待施工'>
        <my-order :order="unconstructionOrder"></my-order>
      </Tab-pane>
      <Tab-pane label="已完成" name='已完成'>
        <my-order :order="completedOrder"></my-order>
      </Tab-pane>
    </Tabs>
  </div>
</template>

<script>
import { Tabs, TabPane, Message } from 'iview'
import myOrder from './component/myorder'
import appConfig from '../libs/app.config'
export default {
  name: 'indent',
  components: { Tabs, TabPane, myOrder, Message },
  data () {
    return {
      activeName: '全部',
      count: '0',
      allOrder: [],
      // unpaidOrder: [], // 待支付
      unconfirmedOrder: [], // 待确认
      unconstructionOrder: [], // 待施工
      completedOrder: [], // 已完成
      labelConstruction: (h) => {
        return h('div', {
          style: {
            position: 'relative'
          }
        }, [
          h('span', '待施工'),
          h('span', {
            class: 'badge'
          }, this.count > 99 ? '99+' : this.count)
        ])
      }
    }
  },
  async mounted () {
    this.allOrder = await this.init(0)
    let countLength = await this.init(2)
    // this.count = 11
    this.count = countLength.length
    document.body.scrollTop = 0
    document.documentElement.scrollTop = 0
  },
  methods: {
    async handleClick (tab, event) {
      this.activeName = tab
      this.allOrder = []
      this.unconfirmedOrder = []
      this.unconstructionOrder = []
      this.completedOrder = []
      if (this.activeName === '全部') {
        this.allOrder = await this.init(0)
      } else if (this.activeName === '待确认') {
        this.unconfirmedOrder = await this.init(1)
      } else if (this.activeName === '待施工') {
        this.unconstructionOrder = await this.init(2)
      } else if (this.activeName === '已完成') {
        this.completedOrder = await this.init(11)
      }
    },
    init (count) {
      let _self = this
      let params = count === 0 ? {
        phone: appConfig['METHODS'].getCookie('phone')
      } : {
        phone: appConfig['METHODS'].getCookie('phone'),
        orderStatus: count
      }
      return new Promise((resolve, reject) =>
        _self.$http.get('/optional/h5/order/user/list', params).then(res => {
          if (res && res.data.code === 200) {
            resolve(res.data.data)
          } else {
            Message.error(res.data.msg)
          }
        }))
    },
    back () {
      history.go(-1)
    }
  }
}
</script>

<style lang="less">
.indent {
  position: relative;
  .grey-bg {
    height: 10px;
    background: #f2f2f2;
  }
  .top-gray {
    position: absolute;
    top: 40px;
    width: 100%;
  }
  .badge {
    // width: 17px;
    height: 14px;
    line-height: 14px;
    padding: 1px 3px;
    border-radius: 50%;
    text-align: center;
    font-size: 10px;
    background: #ff3d3d;
    color: #fff;
    position: absolute;
    margin-top: -2px;
    margin-left: 2px;
  }
  .head {
    text-align: center;
    height: 44px;
    font-size: 16px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: #333;
    line-height: 44px;
    position: fixed;
    width: 100%;
    top: 0;
    background: #fff;
    z-index: 999;
    border-bottom: 1px #ddd solid;
    img {
      width: 22px;
      float: left;
      margin-top: 12px;
      margin-left: 10px;
    }
    .font {
      display: inline-block;
      margin-left: -42px;
    }
  }
  .ivu-tabs {
    // margin-top: 50px;
    .ivu-tabs-tab {
      color: #939393;
      border-bottom: 2px solid transparent;
      padding: 0;
      margin: 8px 16px;
      height: 62%;
      width: 15%;
    }
    .ivu-tabs-tab-active,
    .ivu-tabs-tab-focused {
      color: #df3427;
      border-bottom: 2px solid #df3427 !important;
    }
    .ivu-tabs-bar {
      border-bottom: none;
    }
    .ivu-tabs-ink-bar {
      display: none;
      background-color: #df3427;
      // width: 38px !important;
      // margin-left: 47px;
      // margin-right: 17px;
    }
    // .ivu-tabs-nav div:nth-child(2) {
    //   // padding-left: 20px;
    // }
    .ivu-tabs-nav {
      width: 100%;
      height: 40px;
      display: inline-block;
      text-align: center;
    }
  }
}
</style>



